.card_stars {
  display: flex;
}

.card_stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card_stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #dbd9d9;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  top: 1px;
  left: 1px;
}

.card_image {
  width: 160px;
  height: 134px;
  padding: 32px 19px;
}

.card_content {
  padding: 0 16px;
}

.card_title {
  text-align: left;
  color: #060b35;
  margin: 4px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card_product_code {
  text-align: left;
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card_reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin: 16px 0 24px;
  display: flex;
}

.card_review {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card_product_price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card_text_price {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card_price {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card_button_buy {
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: 0;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card_button_buy:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}
